$(function () {

  $('.slider-banner').slick({
    dots: true,
    prevArrow: '<button class="slider-banner__btn slider-banner__btn_prev"><img src="img/icons/arrow-prev.svg" alt="arrow-prev"</button>',
    nextArrow: '<button class="slider-banner__btn slider-banner__btn_next"><img src="img/icons/arrow-next.svg" alt="arrow-next"</button>',
  });

  $('.tab-item').on('click', function (e) {
    e.preventDefault();

    $($(this).siblings()).removeClass('tab-item_active');
    $($(this).parent().siblings().find('.tab-content')).removeClass('tab-content_active');

    $(this).addClass('tab-item_active');
    $($(this).attr('href')).addClass('tab-content_active');
  });

  $('.item-products__favorites').on('click', function () {
    $(this).toggleClass('item-products__favorites_active')
  });

  $('.content-tabs-products__slider').slick({
    infinite: true,
    slidesToShow: 4,
    slidesToScroll: 1,
    prevArrow: '<button class="slider-products__btn slider-products__btn_prev"><img src="img/icons/arrow-prev_dark.svg" alt="arrow-prev_dark"</button>',
    nextArrow: '<button class="slider-products__btn slider-products__btn_next"><img src="img/icons/arrow-next_dark.svg" alt="arrow-next_dark"</button>',
  });

  $('.filter-style').styler();

  $('.aside-filter__item-drop').on('click', function(){
    $(this).toggleClass('aside-filter__item-drop_active');
    $(this).next().slideToggle(200);
  })
});

